:root {
  --soft-orange: hsl(35, 77%, 62%);
  --soft-red: hsl(5, 85%, 63%);
  --off-white: hsl(36, 100%, 99%);
  --greyish-blue: hsl(233, 8%, 79%);
  --dark-grayish-blue: hsl(236, 13%, 42%);
  --very-dark-blue: hsl(240, 100%, 5%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  background-color: var(--off-white);
  font-family: var(--font-inter);
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

p {
  font-size: 0.9375rem;
}

ul {
  list-style: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  margin: 1.5rem auto 2.875rem;
  max-width: 69.375rem;
  padding: 0 1.25rem;
  height: 2.5rem;
}
@media (min-width: 47.9375em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  justify-content: space-between;
}
.header img {
  height: 2.5rem;
  margin: 0;
  padding: 0;
}

.nav {
  padding: 0;
  margin: 0;
}
.nav__toggle {
  height: 2.5rem;
  width: 2.5rem;
  margin: auto;
  position: relative;
  z-index: 2;
}
.nav__toggle:checked ~ .nav__container {
  transform: translate(-100%);
}
@media (min-width: 47.9375em) {
  .nav__toggle {
    display: none;
  }
}
.nav__container {
  background: var(--off-white);
  height: 100vh;
  width: 65vw;
  position: absolute;
  left: 100vw;
  top: 0;
  transition: transform 0.5s ease-in-out;
  z-index: 1;
}
@media (min-width: 47.9375em) {
  .nav__container {
    height: 100%;
    width: 31.25rem;
    position: static;
    transform: none;
    transition: none;
  }
}
.nav__navlist {
  margin-top: 8.125rem;
  padding-left: 1.875rem;
}
@media (min-width: 47.9375em) {
  .nav__navlist {
    display: flex;
    margin: 0;
    padding: 0;
  }
}
.nav__navlink {
  padding-bottom: 1.875rem;
}
@media (min-width: 47.9375em) {
  .nav__navlink {
    padding-right: 1.25rem;
  }
}
.nav__navlink a {
  color: var(--very-dark-blue);
  cursor: pointer;
}
.nav__navlink a:hover {
  color: var(--soft-red);
}